import React, { Component } from 'react'
import StampContainerImage from './StampContainerImage'

export class EditStampForm extends Component {
  constructor(props) {
    super(props)  
    
    this.state = {
      name:(this.props.stamp != null) ? this.props.stamp.name : '',
      price:(this.props.stamp != null) ? this.props.stamp.price : '',
      country:(this.props.stamp != null) ? this.props.stamp.country:'',
      yearPublished:(this.props.stamp != null) ? this.props.stamp.yearPublished:'',
      isStamped:(this.props.stamp != null) ? this.props.stamp.isStamped:'',
      imageUrl:(this.props.stamp != null) ? this.props.stamp.imageUrl:''
    }
  }

  nameChange = (event) => {
    this.setState({
      name: event.target.value
    })
  }

  priceChange = (event) => {
    this.setState({
      price: event.target.value
    })
  }

  countryChange = (event) => {
    this.setState({
      country: event.target.value
    })
  }

  yearPublishedChange = (event) => {
    this.setState({
      yearPublished: event.target.value
    })
  }

  isStampedChange = (event) => {
    this.setState({
      isStamped: event.target.value
    })
  }

  imageUrlChange = (event) => {
    this.setState({
      imageUrl: event.target.value
    })
  }

  submit = (event) => {
    event.preventDefault();
  }

  image = () => {
    return <StampContainerImage image={this.state.imageUrl}/>
  }

  render() {
    return (
      <div>
        <div className='containerImage'>
          <img src={this.state.imageUrl} alt="StampPic"/>
        </div>
        <form onSubmit={this.submit}>
          <div>
            <label>Name</label>
            <input type='text' value={this.state.name} onChange={this.nameChange}/>
          </div>
          <div>
            <label>Price</label>
            <input type='text' value={this.state.price} onChange={this.priceChange}/>
          </div>
          <div>
            <label>Country</label>
            <input type='text' value={this.state.country} onChange={this.countryChange}/>
          </div>
          <div>
            <label>Year Published</label>
            <input type='text' value={this.state.yearPublished} onChange={this.yearPublishedChange}/>
          </div>
          <div>
            <label>Stamped?</label>
            <input type='text' value={this.state.isStamped} onChange={this.isStampedChange}/>
          </div>
          <div>
            <label>Image Url</label>
            <input type='text' value={this.state.imageUrl} onChange={this.imageUrlChange}/>
          </div>
          <button type='submit'>submit</button>
        </form> 
      </div>
    )
  }
}

export default EditStampForm